import React from 'react';

import './index.scss';

import UnitSearch from './UnitSearch';
import Transitions from './Transitions';

import { IUnit } from '../../orbit-reel';
import { ITransition } from '../App';

interface IProps {
  currentMode: string;
  onFilterChange: (filter: (unit: IUnit) => boolean) => void;
  transitions: ITransition[];
  activeTransition: string;
  onTransition: (transition: string) => void;
  isActionBarCollapsed: boolean;
  onActionBarCollapse: (collapsed: boolean) => void;
}

type Props = IProps;

const ActionBar: React.SFC<Props> = ({
  currentMode,
  onFilterChange,
  transitions,
  activeTransition,
  onTransition,
  isActionBarCollapsed,
  onActionBarCollapse,
}) => {
  return !isActionBarCollapsed ? (
    <div className="action-bar-container">
      <div
        className="collapse-container"
        onClick={() => onActionBarCollapse(true)}>
        <span>&#8250;</span>
      </div>
      {currentMode === 'units' ? (
        <UnitSearch onFilterChange={onFilterChange} />
      ) : (
        <Transitions
          transitions={transitions}
          activeTransition={activeTransition}
          onTransition={onTransition}
        />
      )}
    </div>
  ) : (
    <div
      className="exit-collapse-container"
      onClick={() => onActionBarCollapse(false)}>
      <span>&#8250;</span>
    </div>
  );
};

export default ActionBar;
